<template>
  <div class="user-show">
    <section class="section">
      <div class="container">
        <div class="columns is-multiline">
          <div class="column is-4">
            <div class="box pb-0">
              <figure class="is-center image is-48x48">
                <avatar :username="$store.state.auth.user.name"></avatar>
                <!-- <img class="is-rounded" src="https://bulma.io/images/placeholders/128x128.png"> -->
              </figure>
              <br>

              <hr>
              <div class="px-3 pb-5 content has-text-centered">
                <a href="#" class="">{{ user.name }}</a>
              </div>
            </div>
          </div>
          <div class="column is-8">
            <div class="bloquito">
              <div class="box ">
                <b-field
                  label="Email">
                  <b-input
                    disabled
                    :value="user.email"
                    ></b-input>
                </b-field>
                <hr>
                <a href="#" @click="activeChangePassword" v-if="!changePassword">Change Password</a>
                <template v-if="changePassword">
                  <b-field label="New Password">
                    <b-input
                      type="password"
                      v-model="form.newPassword"
                      ></b-input>
                  </b-field>
                  <b-field label="Password Repeat">
                    <b-input
                      type="password"
                      v-model="form.repeatPassword"
                      ></b-input>
                  </b-field>
                  <b-field label="Current Password">
                    <b-input
                      type="password"
                      v-model="form.currentPassword"
                      ></b-input>
                  </b-field>
                  <b-fiel>
                    <b-button
                      type="is-primary"
                      label="Save"
                      class="mr-2"
                      @click.prevent="submitChangePassword"
                      ></b-button>
                    <b-button
                      type="is-danger"
                      label="Cancel"
                      class="ml-2"
                      @click="cancelChangePassword"
                      ></b-button>
                  </b-fiel>
                </template>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import User from '@/api/User'

import Avatar from 'vue-avatar'

export default {
  name: 'UserShow',
  components: {
    Avatar
  },
  data () {
    return {
      user: null,
      changePassword: false,
      form: {
        newPassword: null,
        repeatPassword: null,
        currentPassword: null
      }
    }
  },
  methods: {
    getUser: function (){
      console.log(this.$store.state.auth.user)
      this.user = this.$store.state.auth.user
    },

    activeChangePassword: function () {
      this.changePassword = !this.changePassword
    },

    submitChangePassword: function () {
      User.changePassword(this.form, this.$store.state.auth.user.id).then((response) => {
        console.log(response)
      }).catch((errors) => {
        console.log(errors)
      })
    },

    cancelChangePassword: function () {
      this.form.newPassword = null
      this.form.repeatPassword = null
      this.form.currentPassword = null
      this.changePassword = !this.changePassword
    }
  },
  mounted () {
    this.getUser()
  }
}
</script>
<style>
  .is-center {
    margin-left: auto;
    margin-right: auto;
  }
</style>
